/*Styles for the PHPizza skin (not including colors, that will be handled seperately*/
header {
    box-shadow: 2px 2px 10px gray;
}

header > ul, footer > ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header > ul > li {
    display: inline-flex;
    margin-right: 10px;
}

header > ul > li > a {
    text-decoration: none;
    font-size: 16px;
}

body {
    font-family: "Inter", "Roboto", sans-serif; 
}

article {
    user-select: text !important;
    -webkit-user-select: text !important;
}

header > ul > li > p > a {
    display:flex;
    align-items: flex-start;
    text-decoration: none;
    /*font-size: 24px;*/
    margin:auto;
}

header > ul > li > p > a > img{
    height:64px;
    width:64px;
}